.editingContainer {
    position: relative;
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 90px;
    text-align: center;
}

.titleText {
    color: white;
    font-size: 50px;
    font-family: 'Monthoers';
}

.bioText {
    color: white;
    font-size: 25px;
    font-family: 'NEB';
}
.bioField {
    position: relative;
    margin: auto;
    border: none;
    font-size: 30px;
    font-family: 'NEB';
    background: none;
    text-decoration: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: 0;
    resize: none;
    color: white;
    background-color: rgba(255,255,255,0.5);
}


::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color: white;
    opacity: 0.35;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color: white;
   opacity: 0.35;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
   color: white;
   opacity: 0.35;
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
   color: white;
   opacity: 0.35;
}
::-ms-input-placeholder { /* Microsoft Edge */
   color: white;
   opacity: 0.35;
}


